import reset from "../../utils/reset";
import Error from '/src/images/error.webp';

// FontAwesome import 
import "@fortawesome/fontawesome-free/css/all.min.css";

const notfound = () => {
  reset('404 Page Not Found')

  return {
    html: /* html */ `
      <section class="min-h-screen flex items-center justify-center text-lg">
        <div class="w-full max-w-3xl mx-auto px-4 py-16">
          <div class="error__item text-center">
            <div class="mb-10 flex justify-center">
              <img src="${Error}" alt="Error 404"
                class="w-full max-w-md h-auto object-cover rounded-3xl" />
            </div>
            <div>
              <h3 class="font-extrabold mb-6 text-brand-primary drop-shadow-lg flex items-center justify-center gap-3">
                <i class="fas fa-exclamation-triangle text-brand-primary"></i>
                Page not found
              </h3>
              <p class="text-lg text-brand-white mb-10 font-medium">Oops! The page you are looking for does not exist. It might have been moved or deleted.</p>
              <a href="/" data-nav class="hero-fade-in inline-flex items-center justify-center px-8 py-4 border-2 border-brand-primary text-brand-primary font-bold rounded-xl hover:bg-brand-primary/10 transition-all hover:translate-y-[-2px] active:translate-y-[0px] text-sm">
                <i class="fas fa-arrow-left text-brand-primary text-xl mr-2"></i>
                Back To Home
              </a>
            </div>
          </div>
        </div>
      </section>
    `,
  };
};

export default notfound;